// 导航栏组件
import cuCustom from '@/colorui/components/cu-custom.vue';
import Modal from '@/components/x-modal/x-modal'
export default {
	components: {
		cuCustom,
		Modal
	},
	data() {
		return {
			// 控制自定义导航栏是否字体加粗
			isBold: true,
			// 商品数据
			Productdata: {},
			// 商品的id
			commodityId: 2,
			// 地址id
			id: {},
			// 用户id
			userid: 1,
			// 地址
			sites: {},
			// 订单
			indent: {},
			show: false,
		};
	},
	methods: {
		// 获取商品数据
		commodity() {
			uni.request({
				url: 'http://47.104.29.236:9999/api/v1/purchase/' + this.commodityId,
				method: 'POST',
				data: {},
				success: res => {
					this.Productdata = res.data.data[0];
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 获取地址
		site() {
			// 获取数据
			uni.request({
				url: 'http://47.104.29.236:9999/api/v1/purchaseSite',
				method: 'POST',
				data: { id: this.id, userid: this.userid },
				success: res => {
					this.sites = res.data.data;
					// console.log(res)
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 跳换地址
		changeSite() {
			uni.navigateTo({
				url: '../xy-site/XYsite',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		// 确认付款
		payment() {
			this.show = true

		},
		confirm(e){
		  uni.request({
		  	url: 'http://47.104.29.236:9999/api/v1/payment',
		  	method: 'POST',
		  	data: { commodityId: this.commodityId, userid: this.userid },
		  	success: res => {
		  		let price = res.data.data;
		  		// 状态
		  		let code = res.data.code;
		  		// console.log(res)
		  		// console.log(code)
		  		// 付款成功
		  		if (code == '400') {
		  			// 生成订单
		  			console.log(1)
		  			uni.request({
		  				url: 'http://47.104.29.236:9999/api/v1/createorder',
		  				method: 'POST',
		  				data: { price: price, userid: this.userid, sites: this.sites , commodityId: this.commodityId},
		  				success: res => {
		  					// 订单创建成功
		  					if (res.data.code == 400) {
		  						uni.navigateTo({
		  							url: '../payment/success',
		  							success: res => {},
		  							fail: () => {},
		  							complete: () => {}
		  						});
		  					}
		  				},
		  				fail: () => {},
		  				complete: () => {}
		  			});
		  		} else {
		  			//付款失败,余额不足
		  			uni.navigateTo({
		  				url: '../payment/failure',
		  				success: res => {},
		  				fail: () => {},
		  				complete: () => {}
		  			});
		  		}
		  	},
		  	fail: () => {},
		  	complete: () => {}
		  });
		},
		event(e){
		// console.log(e)
		},
	},
	onLoad(e) {
		this.id = e;
		this.site();
		let that = this;
		uni.getStorage({
		    key: 'storage_key',
		    success: function (res) {
				that.commodityId = res.data.shopid
		    }
		});
		this.commodity();
		uni.getStorage({
		    key: 'user_id',
		    success: function (res) {
		        // console.log(res.data);
				this.userid = res.data
		    }
		})
	}
};
